*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  --_primary_color: #064724;
  --_secondary_color: #d2a4b1;
  --_back_bright_icon_url: url("back01_bright.3432b3cd.svg");
  background-color: var(--_secondary_color);
}

.headbar {
  background-color: var(--_primary_color);
}

.container {
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  display: flex;
}

.headbar {
  flex-grow: 0;
  justify-content: start;
  width: 100%;
  height: -moz-fit-content;
  height: fit-content;
  padding: .3em .3em .5em .5em;
  font-size: 1rem;
  display: flex;
}

.headbar_item {
  padding: .75em 1em;
}

.headbar_item[action="go_back"] {
  background-image: var(--_back_bright_icon_url);
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto 80%;
  margin-right: .5em;
  display: block;
}

main {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100%;
  display: flex;
  overflow: scroll;
}

.oops_container {
  color: #eee;
  width: calc(100px + 25vw);
  height: -moz-fit-content;
  height: fit-content;
  margin: 10px;
  display: block;
}

.oops_img_container, .oops_img {
  width: 100%;
  height: 100%;
}

.oops_sign {
  text-align: center;
  margin-top: 5px;
}

.oops_description {
  text-align: center;
  margin-top: 5px;
  font-size: 1rem;
  font-weight: bold;
}

